/** Colores **/
/** Colores de anterior pagina **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Inter", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 1.6rem;
  color: #000;
  line-height: 1.5;
}

a {
  text-decoration: none;
}

.contenedor {
  max-width: 90rem;
  width: 90%;
  margin: 0 auto;
}

h1,
h2,
h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
  margin: 0;
}

h3 {
  font-size: 3rem;
}

.conjunto-botones {
  display: flex;
  flex-direction: row;
  gap: 3.5rem;
}
.conjunto-botones i {
  padding: 0;
}

.centrar-flex {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.btn {
  background-color: #fff;
  color: #000;
  font-weight: 700;
  padding: 1.5rem 2.5rem;
  cursor: pointer;
  outline: none;
  border: none;
  transition: 0.3s all ease;
}
.btn:hover {
  background-color: #e0ff4f;
}

.btn-principal {
  background-color: #00272b;
  color: #fff;
  border: 0.1rem solid rgba(0, 0, 0, 0);
}
.btn-principal:hover {
  color: #000;
  background-color: #e0ff4f;
  border: 0.1rem solid #000;
}

.btn-secundario {
  background-color: #e0ff4f;
  border: 0.1rem solid rgba(0, 0, 0, 0);
}
.btn-secundario:hover {
  background-color: #fff;
  border: 0.1rem solid #000;
}

.btn-responder-examen {
  color: #00272b;
  background-color: #e0e0e0;
  padding: 1rem 2rem;
  font-weight: 700;
  transition: 0.3s all ease;
}
.btn-responder-examen:hover {
  background-color: #b3b3b3;
}

.btn-subrayado {
  color: #000;
  padding: 1.5rem 0;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-bottom: 0.1rem solid #000;
  transition: 0.3s all ease;
}
.btn-subrayado:hover {
  color: #242424;
  border-bottom-color: #242424;
}

.no-mg {
  margin: 0;
}

.guia {
  outline: 0.1rem solid red;
}

header {
  background-color: #00272b;
  padding: 8rem 0;
  margin-bottom: 3rem;
}
header .titulo-pagina {
  font-size: 6rem;
  font-weight: 800;
  color: #fff;
  margin: 0 0 3.5rem 0;
}
header .btn-subrayado {
  color: #fff;
  border-bottom-color: #fff;
}
header .btn-subrayado:hover {
  color: #e7e7e7;
  border-bottom-color: #e7e7e7;
}

.menu {
  display: flex;
  justify-content: flex-end;
  position: absolute;
  top: 3rem;
  right: 5rem;
}
.menu .usuario {
  color: #fff;
  margin-right: 2.5rem;
}

footer {
  margin-top: 5rem;
  padding: 5rem 0;
  background-color: #00272b;
}
footer p {
  color: #fff;
  text-align: center;
  font-size: 1.8rem;
  font-weight: 700;
  margin: 2rem 0 0 0;
}

.conjunto-iconos-footer {
  border-bottom: 0.1rem solid #fff;
  width: 90rem;
  padding-bottom: 2rem;
}

.alerta {
  text-align: center;
  margin: 2.5rem 0 -2.5rem 0;
  padding: 1.5rem;
  border-radius: 0.8rem;
  color: #fff;
  text-transform: uppercase;
  font-weight: 700;
}

.exito {
  background-color: #31af01;
}

.error {
  background-color: #f10a0a;
}

table {
  margin-top: 3rem;
  margin-bottom: 5rem;
  width: 100%;
}
table th {
  font-size: 1.8rem;
  color: #fff;
}

.t-center {
  text-align: center;
}

.t-lg {
  width: 80%;
}

table, td, th {
  border-collapse: collapse;
}

td {
  border-right: 0.1rem solid #b3b3b3;
}

th:last-of-type, td:last-of-type {
  border-right: none;
}

table thead {
  text-align: left;
}

th {
  background-color: #00272b;
  color: #fff;
  font-size: 1.8rem;
  padding: 2rem;
}

table tbody tr td {
  padding: 2rem;
}

/* Bg-color gris cada 2 filas */
table tbody tr:nth-of-type(even) {
  background-color: #e0e0e0;
}

/* Borde al ultimo elemento de la tabla */
table tbody tr:last-of-type {
  border-bottom: 0.3rem solid #00272b;
}

.no-registros {
  text-align: center;
  font-weight: 700;
  padding: 2rem 0;
}

table form {
  display: inline;
}

.btn-eliminar {
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

.icono {
  font-size: 1.6rem;
  padding: 1rem;
  border-radius: 50%;
  margin: 0;
  cursor: pointer;
  transition: 0.3s ease all;
}

.icono-editar {
  color: #fff;
  background-color: #e0e0e0;
  color: #000;
}
.icono-editar:hover {
  background-color: #b3b3b3;
}

.icono-borrar {
  color: #fff;
  background-color: #f10a0a;
}
.icono-borrar:hover {
  background-color: #db0707;
}

.conjunto-iconos {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

.icono-social {
  height: 4rem;
  width: 4rem;
  border-radius: 50%;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s all ease;
}
.icono-social:hover {
  background-color: #cae740;
}
.icono-social .icono-footer {
  color: #000;
}

.icono-sesion {
  margin-left: 0.5rem;
}

.form_registro {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: min(60rem, 90%);
  gap: 2rem;
  padding: 5rem;
  margin: 2rem 0;
}

.form__col {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
}

.form__label {
  width: 40%;
  align-self: center;
  text-align: end;
  margin-right: 2rem;
}

.form__input {
  width: 82%;
}
.form__input input, .form__input select {
  outline: 0.1rem solid #c1c1c1;
  padding: 1.2rem;
  border-radius: 0.5rem;
  border: none;
  transition: 0.3s all ease;
}
.form__input input:focus, .form__input select:focus {
  outline: 0.1rem solid #757575;
}
.form__input input {
  width: 100%;
}
.form__input input::placeholder {
  color: #757575;
}

.form__cantidad {
  position: relative;
}
.form__cantidad .signo_cantidad {
  margin: 0;
  color: #757575;
  position: absolute;
  top: 50%;
  left: 1.2rem;
  transform: translate(-50%, -50%);
}
.form__cantidad #cantidad {
  padding-left: 2rem;
}

.form_actualizar-cantidad {
  width: 50%;
  padding: 0;
}

.form__input-100, .form__label-100 {
  width: 100%;
}

.form__col-margin-top {
  margin-top: 1rem;
}

.form__pregunta {
  width: 20%;
}

.form_examen {
  width: min(90%, 90rem);
  padding: 3rem;
}
.form_examen .intrucciones-examen {
  font-size: 2rem;
  font-weight: 800;
  margin: 0;
}
.form_examen .separador {
  border: none;
  background-color: #b3b3b3;
  height: 0.1rem;
  margin: 2.5rem 0;
}

.form_grupo {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.form_grupo input, .form_grupo select {
  outline: 0.1rem solid #c1c1c1;
  padding: 1.2rem;
  border-radius: 0.5rem;
  border: none;
  transition: 0.3s all ease;
}
.form_grupo input:focus, .form_grupo select:focus {
  outline: 0.1rem solid #757575;
}
.form_grupo .form_title {
  width: 100%;
}

.form_grupo-pregunta {
  flex-direction: column;
  margin: 2rem 0 4rem 0;
}
.form_grupo-pregunta .fila {
  display: flex;
  flex-direction: row;
  width: 100%;
  gap: 2rem;
}

.numero-pregunta {
  font-weight: 700;
  height: 3.6rem;
  width: 3.6rem;
  border-radius: 50%;
  padding: 1rem;
  text-align: center;
  background-color: #e0ff4f;
}

.opcion-correcta {
  text-align: end;
}

.pregunta {
  width: 85%;
}

.input-opciones {
  width: 100%;
  flex-direction: row;
  margin-left: 5.5rem;
}
.input-opciones .opcion {
  display: flex;
  align-items: center;
  gap: 2rem;
  margin: 1rem 0;
}
.input-opciones label {
  font-weight: 700;
}
.input-opciones input {
  width: 100%;
}

.btn-examen {
  width: 100%;
}